import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  createUser,
  checkUser,
  updateUser,
  signOut,
  checkAuth,
} from "./AuthAPI";

const IDLE = "idle";
const LOADING = "loading";

const initialState = {
  loggedInUserToken: null,
  status: IDLE,
  error: null,
  userChecked: false,
};

export const createUserAsync = createAsyncThunk(
  "auth/createUser",
  async (data) => {
    const response = await createUser(data);
    return response;
  }
);

export const updateUserAsync = createAsyncThunk(
  "auth/updateUser",
  async (data) => {
    const response = await updateUser(data);
    return response;
  }
);

export const checkUserAsync = createAsyncThunk(
  "auth/checkUser",
  async (data) => {
    const response = await checkUser(data);
    return response.data;
  }
);

// This checks the session is still active for any logged in user so that the user doesnt has to login again & again.
export const checkAuthAsync = createAsyncThunk("auth/checkAuth", async () => {
  try {
    const response = await checkAuth();
    return response.data;
  } catch (error) {
    console.log(error);
  }
});

export const signOutAsync = createAsyncThunk("auth/signOut", async () => {
  const response = await signOut();
  return response.data;
});

export const userSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    nullifyAuth: (state) => {
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = LOADING;
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = IDLE;
        state.loggedInUserToken = action.payload;
        state.error = null;
      })
      .addCase(checkUserAsync.pending, (state) => {
        state.status = LOADING;
      })
      .addCase(checkUserAsync.fulfilled, (state, action) => {
        state.status = IDLE;
        state.loggedInUserToken = action.payload;
        state.error = null;
      })
      .addCase(checkUserAsync.rejected, (state, action) => {
        state.status = IDLE;
        state.error = action.error;
      })
      .addCase(updateUserAsync.pending, (state) => {
        state.status = LOADING;
      })
      .addCase(updateUserAsync.fulfilled, (state, action) => {
        state.status = IDLE;
        state.loggedInUserToken = action.payload;
        state.error = null;
      })
      .addCase(signOutAsync.pending, (state) => {
        state.status = LOADING;
      })
      .addCase(signOutAsync.fulfilled, (state) => {
        state.status = IDLE;
        state.loggedInUserToken = null;
        state.error = null;
      })
      .addCase(checkAuthAsync.pending, (state) => {
        state.status = LOADING;
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = IDLE;
        state.loggedInUserToken = action.payload;
        state.error = null;
        state.userChecked = true;
      })
      .addCase(checkAuthAsync.rejected, (state, action) => {
        state.status = IDLE;
        state.userChecked = true;
      });
  },
});

export const { nullifyAuth } = userSlice.actions;

export default userSlice.reducer;

// UseSelector's
export const SelectLoggedInUserToken = (state) =>
  state.authIndex.loggedInUserToken;
export const SelectError = (state) => state.authIndex.error;
export const SelectUserChecked = (state) => state.authIndex.userChecked;
