export function createUser(userData) {
  console.log(userData, "userData");
  return new Promise(async (resolve) => {
    const response = await fetch("/auth/signup", {
      method: "POST",
      body: JSON.stringify(userData),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve(data);
  });
}

export function checkUser(loginData) {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/auth/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(loginData),
      });
      const data = await response.json();
      if (response.ok) {
        resolve({ data });
      } else {
        reject({ message: data.message || "Failed to authenticate" });
      }
    } catch (err) {
      reject({ err: err, message: "Network error" });
    }
  });
}

export function checkAuth() {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/auth/check");
      if (response.ok) {
        const data = await response.json();
        resolve({ data });
      } else {
        const error = await response.text();
        reject(error);
      }
    } catch (error) {
      reject(error);
    }
  });
}

export function updateUser(updatedData) {
  return new Promise(async (resolve) => {
    const response = await fetch("/users/" + updatedData.id, {
      method: "PATCH",
      body: JSON.stringify(updatedData),
      headers: { "content-type": "application/json" },
    });
    // console.log(response, "response");
    const data = await response.json();
    // console.log(data, ":data");
    resolve(data);
  });
}

export function signOut(userId) {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/auth/logout");
      if (response.ok) {
        resolve({ data: "success" });
      } else {
        const error = await response.text();
        reject(error);
      }
    } catch (error) {
      console.log(error);
      reject(error);
    }
  });
}
