import React, { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import { useSelector } from "react-redux";

import CheckoutForm from "./StripeCheckoutForm";
import "../Stripe.css";
import { SelectCurrentOrder } from "../Feature/Order/OrderSlice";

// Make sure to call loadStripe outside of a components render to avoid
// recreating the Stripe object on every render.
// This is your test publishable API key.
const stripePromise = loadStripe(
  "pk_test_51PL9zVSBSeFkkdMDIRSS22yA9z9e5nw1TDCeyNgE7QrEQhMoi84AfrUXDkgvsd52YAnCNJEUvEw5GH3d6nGCoh3H00qVtp9keF"
);

export default function StripeCheckout() {
  const [clientSecret, setClientSecret] = useState("");
  const currentOrder = useSelector(SelectCurrentOrder);

  useEffect(() => {
    // Create PaymentIntent as soon as the page loads
    fetch("/create-payment-intent", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        // <----------------- This all info is to be sent and received at the backend -------------------->
        // If not error of INDIAN GOVT REGUALTIONS I.E. DESCRIPTION AND BILLING NAME AND ADDRESS REQUIRED.
        orderId: currentOrder.id,
        amount: currentOrder.subtotal,
        description: "Payment for YouBuy",
        shipping: currentOrder.selectedAddress,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data.clientSecret);
        setClientSecret(data.clientSecret);
      });
  }, []);

  const appearance = {
    theme: "stripe",
  };
  const options = {
    clientSecret,
    appearance,
  };

  return (
    <div className="Stripe">
      {clientSecret && (
        <Elements options={options} stripe={stripePromise}>
          <CheckoutForm />
        </Elements>
      )}
    </div>
  );
}
